<style lang="less">
@autoColor: #ffc124; //橘色
@fontColor: #c8c8c8; //灰色

.view_advice{
	.text_area{
		textarea{
		    height: 200px;
		    width: 100%;
		    color: @fontColor;
		    resize: none;
		    overflow-y: scroll;
		    outline: none;
		}
	}
	div.input_other{
		color: @fontColor;
		margin: 10px 0;
		&>a.br{
			float: none;
		}
	}
	.btn_area{
		margin-top: 15px;
		.btn{
			background-color: @autoColor;
		}
	}											
}
</style>

<template>
	<cs-base :view="'advice'">
		<h1 class="title">意见反馈</h1>
		<div class="text_area">
			<textarea name="" v-model="data.advice" placeholder="写下您宝贵的意见"></textarea>
		</div>
		<div class="input_other">
				<input type="checkbox">同步至sns社区并
				<a href="" class="br">@不扔</a>
		</div>
		<div class="btn_area">
			<a class="btn" @click="submitAdvice">提交</a>
		</div>
	</cs-base>
</template>

<script>
import csBase from './base'

export default {
	data() {
		return {
			data: {
				advice: '',
				isLogin: false
			}
		}
	},
	components: {
		csBase
	},
	methods: {
		submitAdvice() {
			const isLogin = localStorage.getItem('userLogin');
			if(isLogin) {
				this.isLogin = isLogin;
			}
			const data = this.data;
			if (!this.isLogin) {
				alert('请先登陆！');
				return;
			}
			if (!data.advice) {
				alert('请填写宝贵意见');
				return;
			} 
			//意见请求
			// fetch("http://xianyijun.cn:8080/suggestion", {
			// 	method: "POST",
			// 	headers: {
			// 		"Content-Type": "application/x-www-form-urlencoded"
			// 	},
			// 	body: `suggestioncontent=${data.advice}`,
			// 	credentials: 'include'
			// }).then((res) => {
			// 	return res.json()
			// }).then(data => {
			// 	if(data.code === 1) {
			// 		alert(data.data);
			// 		window.close();
			// 	} else {
			// 		alert(data.data);
			// 	}
			// });
			alert('提交成功');
			window.close();
		}
	}
}
</script>


